html,
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #000;
  background-color: #eee;
  cursor: default;
}
a:hover,
a:focus,
a:active {
  color: #000;
  text-decoration: none;
}
p,
h1,
h2,
h3,
h4,
h5,
h6,
span {
  margin: 0 ;
  padding: 0;
}
button {
  padding: 0 !important;
  border: 0 !important;
  cursor: pointer;
}
:focus {
  outline: none !important;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
}
.logo {
  font-size: 20px;
  text-transform: uppercase;
  transition: color 0.4s;
  color: #fff;
}
.logo:hover,
.logo:focus {
  color: #00bfff !important;
}
.logo:active {
  color: #1e90ff !important;
}
.site-navigation__toggler {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25px;
  border: 0 !important;
  transition: color 0.4s;
  cursor: pointer;
}
.site-navigation__toggler__button {
  position: relative;
  width: 35px !important;
  height: 3px !important;
  background-color: #fff;
  border-radius: 3px;
  transition: 0.4s;
}
.site-navigation__toggler__button::before {
  content: "";
  position: absolute;
  top: -10px;
  display: block;
  width: 35px;
  height: 3px;
  background-color: #fff;
  border-radius: 3px;
  transition: 0.4s;
}
.site-navigation__toggler__button::after {
  content: "";
  position: absolute;
  bottom: -10px;
  display: block;
  width: 35px;
  height: 3px;
  background-color: #fff;
  border-radius: 3px;
  transition: 0.4s;
}
.site-navigation__toggler__button:focus {
  background-color: #00bfff;
}
.site-navigation__toggler__button:focus::before,
.site-navigation__toggler__button:focus::after {
  background-color: #00bfff;
}
.site-navigation__toggler:hover .site-navigation__toggler__button {
  background-color: #00bfff;
}
.site-navigation__toggler:hover .site-navigation__toggler__button::before,
.site-navigation__toggler:hover .site-navigation__toggler__button::after {
  background-color: #00bfff;
}
.site-navigation__toggler--close .site-navigation__toggler__button {
  background: none;
  transition: 0.4s;
}
.site-navigation__toggler--close .site-navigation__toggler__button:focus::before,
.site-navigation__toggler--close .site-navigation__toggler__button:focus::after {
  background-color: #ff0000;
}
.site-navigation__toggler--close .site-navigation__toggler__button::before {
  top: 0;
  transform: rotate(45deg);
}
.site-navigation__toggler--close .site-navigation__toggler__button::after {
  bottom: 0;
  transform: rotate(-45deg);
}
.site-navigation__toggler--close:hover .site-navigation__toggler__button {
  background: none;
}
.site-navigation__toggler--close:hover .site-navigation__toggler__button::before,
.site-navigation__toggler--close:hover .site-navigation__toggler__button::after {
  background-color: #ff0000;
}
.page-navigation__link {
  transition: color 0.4s;
}
.page-navigation__link:hover,
.page-navigation__link:focus {
  color: rgba(0, 191, 255, 0.75) !important;
}
.page-navigation__link:active {
  color: rgba(30, 144, 255, 0.75) !important;
}
.user-navigation__link {
  transition: color 0.4s;
}
.user-navigation__link:hover,
.user-navigation__link:focus {
  color: rgba(0, 191, 255, 0.75) !important;
}
.user-navigation__link:active {
  color: rgba(30, 144, 255, 0.75) !important;
}
.main-container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  height: auto;
}
.blocks__navigation {
  height: 100%;
}
.blocks__navigation__list {
  list-style: none;
  padding: 0;
  margin: 55px 0 0 0;
}
.blocks__navigation__link {
  color: #000;
  transition: color 0.4s;
}
.blocks__navigation__link:hover,
.blocks__navigation__link:focus {
  color: #00bfff;
}
.blocks__navigation__link:active {
  color: #1e90ff;
}
.teachers {
  flex-grow: 4;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  max-width: 100vw;
  margin: 10px;
  background-color: #fff;
  border-radius: 10px;
}
.teachers__title {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100px;
  margin-bottom: 10px;
}
.teacher {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  height: auto;
  margin-bottom: 30px;
}
.teacher__photo {
  width: 140px;
  height: 140px;
  margin-bottom: 20px;
  text-indent: -200vw;
  border-radius: 50%;
  background-color: #808080;
  overflow: hidden;
}
.teacher__name {
  margin-bottom: 20px !important;
  font-size: 24px;
  font-weight: 500;
  text-align: center;
  line-height: 30px;
}
.teacher__employment {
  font-size: 14px;
  color: #808080;
}
.main-footer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  height: auto;
  padding: 20px 0;
  background-color: #343a40;
}
.footer-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  width: 100%;
  padding: 0 50px;
}
.contacts {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  height: auto;
  margin-bottom: 30px;
}
.contacts__link--main {
  margin-bottom: 10px;
  font-size: 36px;
  text-transform: uppercase;
  transition: color 0.4s;
  color: #fff;
}
.contacts__link--main:hover,
.contacts__link--main:focus {
  color: #00bfff !important;
}
.contacts__link--main:active {
  color: #1e90ff !important;
}
.contacts__link--vk {
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.contacts__link--vk__block {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  margin-right: 20px;
  font-size: 30px;
  color: #fff;
  background-color: #3b5999;
  transition: 0.4s;
}
.contacts__link--vk__title {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 60px;
  transition: color 0.4s;
}
.contacts__link--vk:hover .contacts__link--vk__block,
.contacts__link--vk:focus .contacts__link--vk__block {
  background: #344e87;
}
.contacts__link--vk:hover .contacts__link--vk__title,
.contacts__link--vk:focus .contacts__link--vk__title {
  color: #00bfff;
}
.contacts__link--vk:active .contacts__link--vk__block {
  background: #2d4474;
}
.contacts__link--vk:active .contacts__link--vk__title {
  color: #1e90ff;
}
.contacts__telephone {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  margin-bottom: 10px;
  color: #fff;
}
.contacts__address {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  color: #fff;
}
.contacts__address--map {
  color: #00ced1;
  transition: color 0.4s;
}
.contacts__address--map:hover,
.contacts__address--map:focus {
  color: #00bfff !important;
}
.contacts__address--map:active {
  color: #1e90ff !important;
}
.page-navigation--footer {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: 200px;
  margin-bottom: 30px;
  padding: 0;
  list-style: none;
}
.page-navigation--footer__item:not(:last-of-type) {
  margin-bottom: 10px;
}
.page-navigation--footer__link {
  font-size: 20px;
  color: #fff;
  transition: color 0.4s;
}
.page-navigation--footer__link:hover,
.page-navigation--footer__link:focus {
  color: #00bfff !important;
}
.page-navigation--footer__link:active {
  color: #1e90ff !important;
}
.copyright {
  color: rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.copyright__letter {
  margin-right: 10px;
}
.copyright__title {
  margin-right: 10px;
}
.copyright__authors {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style: none;
}
.copyright__author__link {
  margin-right: 10px;
  color: rgba(255, 255, 255, 0.5);
  transition: color 0.4s;
}
.copyright__author__link:hover,
.copyright__author__link:focus {
  color: #00bfff !important;
}
.copyright__author__link:active {
  color: #1e90ff !important;
}
@media screen and (min-width: 576px) {
  .footer-navigation {
    flex-direction: row;
    align-items: flex-start;
  }
}
